t\">\n                  <RadioGroup vertical size=\"large\" @on-change=\"changeAddress\">\n                    <Radio :label=\"item.addressId\" v-for=\"(item, index) in address\" :key=\"index\">\n                      <span>{{item.name}} {{item.province}} {{item.city}} {{item.address}} {{item.phone}} {{item.postalcode}}</span>\n                    </Radio>\n                  </RadioGroup>\n                </p>\n            </Panel>\n          </Collapse>\n        </div>\n      </div>\n      <div class=\"remarks-container\">\n        <h3>备注</h3>\n        <i-input v-model=\"remarks\" size=\"large\" placeholder=\"在这里填写备注信息\" class=\"remarks-input\"></i-input>\n      </div>\n      <div class=\"invoices-container\">\n        <h3>发票信息</h3>\n        <p>该商品不支持开发票</p>\n      </div>\n      <div class=\"pay-container\">\n        <div class=\"pay-box\">\n          <p><span>提交订单应付总额：</span> <span class=\"money\"><Icon type=\"social-yen\"></Icon> {{totalPrice.toFixed(2)}}</span></p>\n          <div class=\"pay-btn\">\n            <router-link to=\"/pay\"><Button type=\"error\" size=\"large\">支付订单</Button></router-link>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport Search from '@/components/Search';\nimport GoodsListNav from '@/components/nav/GoodsListNav';\nimport store from '@/vuex/store';\nimport { mapState, mapActions } from 'vuex';\nexport default {\n  name: 'Order',\n  beforeRouteEnter (to, from, next) {\n    window.scrollTo(0, 0);\n    next();\n  },\n  created () {\n    this.loadAddress();\n  },\n  data () {\n    return {\n      goodsCheckList: [],\n      columns: [\n        {\n          type: 'selection',\n          width: 58,\n          align: 'center'\n        },\n        {\n          title: '图片',\n          key: 'img',\n          width: 86,\n          render: (h, params) => {\n            return h('div', [\n              h('img', {\n                attrs: {\n                  src: params.row.img\n                }\n              })\n            ]);\n          },\n          align: 'center'\n        },\n        {\n          title: '标题',\n          key: 'title',\n          align: 'center'\n        },\n        {\n          title: '套餐',\n          width: 198,\n          key: 'package',\n          align: 'center'\n        },\n        {\n          title: '数量',\n          key: 'count',\n          width: 68,\n          align: 'center'\n        },\n        {\n          title: '价格',\n          width: 68,\n          key: 'price',\n          align: 'center'\n        }\n      ],\n      checkAddress: {\n        name: '未选择',\n        address: '请选择地址'\n      },\n      remarks: ''\n    };\n  },\n  computed: {\n    ...mapState(['address', 'shoppingCart']),\n    totalPrice () {\n      let price = 0;\n      this.goodsCheckList.forEach(item => {\n        price += item.price * item.count;\n      });\n      return price;\n    }\n  },\n  methods: {\n    ...mapActions(['loadAddress']),\n    select (selection, row) {\n      console.log(selection);\n      this.goodsCheckList = selection;\n    },\n    changeAddress (data) {\n      const father = this;\n      this.address.forEach(item => {\n        if (item.addressId === data) {\n          father.checkAddress.name = item.name;\n          father.checkAddress.address = `${item.name} ${item.province} ${item.city} ${item.address} ${item.phone} ${item.postalcode}`;\n        }\n      });\n    }\n  },\n  mounted () {\n    setTimeout(() => {\n      this.$refs.selection.selectAll(true);\n    }, 500);\n  },\n  components: {\n    Search,\n    GoodsListNav\n  },\n  store\n};\n</script>\n\n<style scoped>\n.goods-list-container {\n  margin: 15px auto;\n  width: 80%;\n}\n.tips-box {\n  margin-bottom: 15px;\n}\n.address-container {\n  margin-top: 15px;\n}\n.address-box {\n  margin-top: 15px;\n  padding: 15px;\n  border: 1px #ccc dotted;\n}\n.address-check {\n  margin: 15px 0px;\n  height: 36px;\n  display: flex;\n  align-items: center;\n}\n.address-check-name {\n  width: 120px;\n  display: flex;\n  justify-content: center;\n  align-content: center;\n  background-color: #ccc;\n}\n.address-check-name span {\n  width: 120px;\n  height: 36px;\n  font-size: 14px;\n  line-height: 36px;\n  text-align: center;\n  color: #fff;\n  background-color: #f90013;\n}\n.address-detail {\n  padding-left: 15px;\n  font-size: 14px;\n  color: #999999;\n}\n.remarks-container {\n  margin: 15px 0px;\n}\n.remarks-input {\n  margin-top: 15px;\n}\n.invoices-container p{\n  font-size: 12px;\n  line-height: 30px;\n  color: #999;\n}\n.pay-container {\n  margin: 15px;\n  display: flex;\n  justify-content: flex-end;\n}\n.pay-box {\n  font-size: 18px;\n  font-weight: bolder;\n  color: #495060;\n}\n.money {\n  font-size: 26px;\n  color: #f90013;\n}\n.pay-btn {\n  margin: 15px 0px;\n  display: flex;\n  justify-content: flex-end;\n}\n</style>\n"],"sourceRoot":""}]);

